body {
    margin: 0;
    position: relative;
    font-size: 16px;
    line-height: 1.5;
    font-family: "TT Lakes", sans-serif;
    letter-spacing: -0.2px;
    background-color: $bgColor;
    color: $main-text-color;
  }
  
  *,
  ::before,
  ::after {
    box-sizing: inherit;
  }
  
  :root {
    box-sizing: border-box;
  }
  
  img {
    max-width: 100%;
    height: auto;
  }
  
  
  